	module.exports = {"data":{"wordpressPost":{"title":"Myth that UK supply of innovative new pharma drugs is drying up","content":"<p>&nbsp;</p>\n<p>The widely held belief that the UK supply of innovative new medicines has conspicuously dwindled in recent decades, is not borne out by the evidence, reveals <a href=\"http://bmjopen.bmj.com/content/3/2/e002088.full\">research published in BMJ Open</a>.</p>\n<p>In fact, the reverse may be true, the figures suggest.</p>\n<p>The prevailing view is that pharmaceutical industry innovation has been in decline, with fewer new drugs launched in recent decades than before—despite more cash being pumped into research and development—prompting a good deal of hand-wringing, say the authors.</p>\n<p>They wanted to find out whether this view was justified, by looking at all new medicines added every year to the prescribing and dispensing drugs bible, the British National Formulary, or BNF for short, over 30 years. The BNF is updated every six months.</p>\n<p>After the US, the UK is the next largest source of new drug development, generating more than 10% of all new medicines around the globe.</p>\n<p>All new synthetic chemical entities and new biological drugs, such as vaccines, blood products, and gene therapies were included, based on their first appearance in the BNF between 1982 and 2011.</p>\n<p>New products covered modifications of existing drugs as well as radical breakthrough treatments. Different doses and formulations containing the same active ingredients were only counted once, and generic versions of brand drugs were excluded.</p>\n<p>There was no significant linear trend pointing to a decline in the number of new drugs introduced into the UK over that period, which averaged just under 24 a year.</p>\n<p>But the authors did find a pattern of peaks and troughs, with dips invariably followed by a surge in new arrivals.</p>\n<p>After a dip in the mid-1980s, with around a dozen new drugs coming on to the market between 1985 and 1987, new arrivals increased every year, peaking at 34 in 1997.</p>\n<p>This peak was again followed by a dip, with around 20 new drugs a year between 2003 and 2006, followed by a further peak in 2010.</p>\n<p>And extending the timeline back to the 1970s indicated an overall slight but significant increase of 0.16 new drugs every year between 1971 and 2011, “contradicting the widely held view that the number of new medicines being launched is declining,” say the authors.</p>\n<p>They conclude that the perceived “innovation dip” is a product of the time periods studied in the past.</p>\n<p>“Although there was indeed a dip in new drug introductions during the decade from 1997 to 2006, this was largely an artefact of a peak in 1997, which was itself preceded by an unusually low number of launches in 1985-87,” they write. “Additionally, the peak number of new drugs added to the BNF in 1997 was matched in 2010.”</p>\n<p>The authors point out that their study does not distinguish between varying degrees of innovation, and launches are not the only indicator of pharmaceutical industry health. But theirs is the most up to date UK study of new launch trends, they say.</p>\n<p>Nevertheless, they do sound a note of caution: the costs of drug development have soared. and the time taken to bring a new drug to market has risen from 3 years in 1960 to 12 in 2000.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"b9a76558-0e6d-59f7-8593-3b8814387533"}}

